import React, {Component} from 'react';
import {
  Modal,
  Button,
  Form,
  Input,
  Tooltip,
  Icon,
  Row,
  Col
} from 'antd';

const InputGroup = Input.Group;
const FormItem = Form.Item;
const styles = {
  paymentInput: {
    fontSize: '0.9rem',
    letterSpcae: '3px',
    fontWeight: '600'
  }
}
class Upgrade extends Component {
  constructor() {
    super();
    this.state = {
      paymentModal1: false,
      submitLoading: false
    }
  }
  triggerPaymentModal1(status) {
    this.setState({paymentModal1: status})
  }
  handleSubmit(e) {
    e.preventDefault();
    this
      .props
      .form
      .validateFields((err, values) => {
        if (!err) {
          console.log('Received values of form: ', values);
          this.setState({submitLoading: true})
          setTimeout(() => {
            this.setState({submitLoading: false})
          }, 1000)
        }
      });
  }
  decorate(numbers) {
    return numbers + '33'
  }
  render() {
    const {getFieldDecorator} = this.props.form;
    return (
      <div className="display-component">
        <div className="header">Upgrade</div>
        <div className="content">
          <div className="container-fluid">
            <div>Upgrade</div>
            <hr/>
            <Button size="large" type="primary" onClick={() => this.triggerPaymentModal1(true)}>Subscribe</Button>
            <Modal
              title="Subscription"
              style={{
              top: 30
            }}
              visible={this.state.paymentModal1}
              onCancel={() => this.triggerPaymentModal1(false)}
              footer={null}>
              <Form
                onSubmit={this
                .handleSubmit
                .bind(this)}
                className="payment-form">
                <FormItem
                  label={(
                  <span>
                    Card Number&nbsp;
                    <Tooltip title="It should be 16 digits.">
                      <Icon type="question-circle-o"/>
                    </Tooltip>
                  </span>
                )}>
                  {getFieldDecorator('cardNumber', {
                    rules: [
                      {
                        required: true,
                        message: 'Please provide your card number!'
                      }
                    ]
                  })(<Input
                    size="large"
                    style={styles.paymentInput}
                    maxLength={16}
                    minLength={16}
                    placeholder="1234 1234 1234 1234"/>)}
                </FormItem>

                <Row>
                  <Col xs={24} md={11}>
                    <FormItem
                      label={(
                      <span>
                        Expiration Data&nbsp;
                        <Tooltip title="It should be on your credit card, under the card number.">
                          <Icon type="question-circle-o"/>
                        </Tooltip>
                      </span>
                    )}>
                      <InputGroup>
                        {getFieldDecorator('expiration.month', {
                          rules: [
                            {
                              required: true,
                              message: 'Please provide your expriation date!'
                            }
                          ]
                        })(<Input
                          maxLength={2}
                          minLength={2}
                          style={{
                          width: '50%',
                          ...styles.paymentInput
                        }}
                          placeholder="MM"/>)}
                        <Input
                          maxLength={2}
                          minLength={2}
                          style={{
                            width: '50%',
                            ...styles.paymentInput
                          }}
                          placeholder="YY"/>
                      </InputGroup>
                    </FormItem>
                  </Col>
                  <Col
                    xs={24}
                    md={{
                    span: 11,
                    offset: 2
                  }}>
                    <FormItem
                      label={(
                      <span>
                        CVC&nbsp;
                        <Tooltip title="It should be 3 digits.">
                          <Icon type="question-circle-o"/>
                        </Tooltip>
                      </span>
                    )}>
                      {getFieldDecorator('cvc', {
                        rules: [
                          {
                            required: true,
                            message: 'Please provide your CVV!'
                          }
                        ]
                      })(<Input
                        size="large"
                        maxLength={3}
                        minLength={3}
                        style={styles.paymentInput}
                        placeholder="123"/>)}
                    </FormItem>
                  </Col>
                  <Col xs={24} md={11}>
                    <FormItem
                      label={(
                      <span>
                        Postal Code&nbsp;
                        <Tooltip title="It should be 5 digits.">
                          <Icon type="question-circle-o"/>
                        </Tooltip>
                      </span>
                    )}>
                      {getFieldDecorator('postalCode', {
                        rules: [
                          {
                            required: true,
                            message: 'Please provide your postal code!'
                          }
                        ]
                      })(<Input
                        size="large"
                        maxLength={5}
                        minLength={5}
                        style={styles.paymentInput}
                        placeholder="11111"/>)}
                    </FormItem>
                  </Col>
                </Row>

                <FormItem>
                  <Button
                    style={{
                    width: "100%"
                  }}
                    size="large"
                    type="primary"
                    htmlType="submit"
                    loading={this.state.submitLoading}>
                    Complete Payment Now
                  </Button>
                </FormItem>
              </Form>
            </Modal>
          </div>
        </div>
      </div>
    )
  }
}

Upgrade = Form.create()(Upgrade);
export default Upgrade;


